.submitForm {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 370px;
    padding-left: 100px;

}

.submitTitle {
    padding-left: 100px;
    font-size: 24px;
    margin-bottom: 35px;
    margin-top: 35px;
}

.label {
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    /* margin-right: ; */
}

.input {
    width: 250px;
    border-radius: 5px;
    border: 1px solid rgb(56, 55, 55);
    padding: 7px 10px;
}

.btn {
    cursor: pointer;
    border-radius: 15px;
    border: 1px solid rgb(56, 55, 55);
    font-size: 16px;
    color: rgb(56, 55, 55);
    background-color: aliceblue;
    padding: 10px 45px;
    /* max-width: 150px; */
    /* margin-left: 150px; */
    margin-left: 0;
    margin-right: auto;
    margin-top: 15px;
}

.btn:hover,
.btn:focus {
    border-color: rgb(28, 113, 49);
    color: rgb(28, 113, 49);
    ;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}